* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Inter", sans-serif;
}

header {
  background-color: #f55a5a;
  height: 60px;
  margin-bottom: 80px;
}

header nav {
  display: flex;
  justify-content: center;
  padding: 20px;
}

header nav h4 {
  margin-left: 9px;
  font-size: 15px;
  color: white;
}

.main-card {
  display: flex;
  padding: 45px;
  /* text-align: center; */
}

.main-photo {
  width: 126px;
  height: 168px;
  border-radius: 8px;
}

.info {
  padding: 20px;
}

.info--location {
  display: flex;
  align-items: center;
  margin-right: 5px;
}

.Country {
  margin-right: 20px;
  margin-left: 10px;
  font-size: 10px;
}

.title {
  font-size: 25px;
  font-weight: bold;
  margin-bottom: 10px;
  margin-top: 8px;
}

.info--location a {
  color: #918e9b;
  font-size: 10px;
}

.data {
  font-size: 10px;
  font-weight: bold;
  margin-bottom: 6px;
}

.descripion {
  font-size: 10px;
  width: 400px;
}

